
























































.tab-bar-container {
  height: 64px;
  .tab-bar-line {
    background-color: rgb(204, 201, 201);
    height: 0.5px;
    width: 100%;
    margin: 0px;
  }
  .tab-bar {
    background-color: white;
    height: inherit;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
  }
}
