.sm_root {
  width: 100%;
}
.sm_root .sm_section {
  list-style: none;
  padding: 0;
  display: inline;
}
.sm_root .sm_section li {
  -webkit-tap-highlight-color: transparent;
  background-color: white;
  height: 50px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 0 20px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  border-width: 1px;
  border-color: #ECECEB;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.sm_root .sm_section li span {
  margin-left: 8px;
  color: #504F50;
  font-size: 15px;
}
.sm_root .sm_section li img {
  width: 14px;
  height: 14px;
  -webkit-transition: transform 0.25s linear;
  -webkit-transition: -webkit-transform 0.25s linear;
  transition: -webkit-transform 0.25s linear;
  transition: transform 0.25s linear;
  transition: transform 0.25s linear, -webkit-transform 0.25s linear;
}
.sm_root .sm_section li .arrow_trans {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  -webkit-transition: transform 0.25s linear;
  -webkit-transition: -webkit-transform 0.25s linear;
  transition: -webkit-transform 0.25s linear;
  transition: transform 0.25s linear;
  transition: transform 0.25s linear, -webkit-transform 0.25s linear;
}
.sm_root .sm_section .sm_s_header {
  margin-top: 10px;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.sm_root .sm_section .sm_s_cell {
  position: relative;
}
.sm_root .sm_section .sm_s_cell img {
  margin-left: 5px;
}
.sm_root .sm_section .sm_s_cell span {
  color: #666566;
}
.sm_root .sm_section .sm_s_cell_last:before {
  content: '';
  position: absolute;
  left: 20px;
  top: auto;
  bottom: 0;
  right: auto;
  height: 1px;
  width: 100%;
  background-color: #ECECEB;
}
