



















































.sm_root {
  width: 100%;
  .sm_section {
    list-style: none;
    padding: 0;
    display: inline;
    li {
      -webkit-tap-highlight-color: transparent;
      background-color: white;
      height: 50px;
      display: flex;
      padding: 0 20px;
      align-items: center;
      border-width: 1px;
      border-color: #ECECEB;
      box-sizing: border-box;
      span {
        margin-left: 8px;
        color: #504F50;
        font-size: 15px;
      }
      img {
        width: 14px;
        height: 14px;
        -webkit-transition: transform .25s linear;
        -moz-transition: transform .25s linear;
        -o-transition: transform .25s linear;
        transition: transform .25s linear;
      }
      .arrow_trans {
        transform: rotate(90deg);
        -webkit-transition: transform .25s linear;
        -moz-transition: transform .25s linear;
        -o-transition: transform .25s linear;
        transition: transform .25s linear;
      }
    }
    .sm_s_header {
      margin-top: 10px;
      justify-content: space-between;
    }
    .sm_s_cell {
      position: relative;
      img {
        margin-left: 5px;
      }
      span {
        color: #666566;
      }
    }
    .sm_s_cell_last:before {
      content: '';
      position: absolute;
      left: 20px;
      top: auto;
      bottom: 0;
      right: auto;
      height: 1px;
      width: 100%;
      background-color: #ECECEB;
    }
  }
}
