.evaluation_root .e_header {
  padding-top: 20px;
  margin-bottom: 20px;
  text-align: center;
}
.evaluation_root .e_header .e_h_score .e_h_s_value {
  font-size: 29px;
}
.evaluation_root .e_header .e_h_score .e_h_s_unit {
  font-size: 9px;
}
.evaluation_root .e_header .e_h_score span {
  color: #FB6C6C;
}
.evaluation_root .e_header .e_h_comment_count {
  font-size: 17px;
  color: #878889;
}
.evaluation_root .e_container .e_c_item {
  position: relative;
  padding: 10px;
}
.evaluation_root .e_container .e_c_item .e_c_i_title {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.evaluation_root .e_container .e_c_item .e_c_i_title .e_c_i_t_left {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.evaluation_root .e_container .e_c_item .e_c_i_title .e_c_i_t_left .e_c_i_t_name {
  font-size: 15px;
  color: #878889;
}
.evaluation_root .e_container .e_c_item .e_c_i_title .e_c_i_t_left .e_c_i_t_star {
  list-style: none;
  padding: 0 0 0 5px;
  margin: 0;
}
.evaluation_root .e_container .e_c_item .e_c_i_title .e_c_i_t_left .e_c_i_t_star .e_c_i_t_s_item {
  padding: 0;
  float: left;
}
.evaluation_root .e_container .e_c_item .e_c_i_title .e_c_i_t_left .e_c_i_t_star .e_c_i_t_s_item img {
  padding: 0;
  margin: 0;
  width: 18px;
  height: 18px;
}
.evaluation_root .e_container .e_c_item .e_c_i_title .e_c_i_t_date {
  font-size: 15px;
  color: #878889;
  white-space: nowrap;
}
.evaluation_root .e_container .e_c_item .e_c_i_detail {
  margin-top: 10px;
}
.evaluation_root .e_container .e_c_item .e_c_i_detail .e_c_i_d_content {
  font-size: 15px;
}
.evaluation_root .e_container .e_c_item .e_c_i_detail .e_c_i_d_images {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.evaluation_root .e_container .e_c_item .e_c_i_detail .e_c_i_d_images img {
  width: 50px;
  height: 50px;
  margin-top: 10px;
  margin-right: 10px;
}
.evaluation_root .e_container .e_c_item:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  bottom: auto;
  right: auto;
  height: 0.5px;
  width: 100%;
  background-color: #EBECED;
}
