






























































































































































































































































































































































.res_root {
  .r_left {
    overflow-y: auto;
    overflow-x: hidden;
    -webkit-overflow-scrolling: touch;
    background-color: #f2f3f4;
    display: flex;
    flex-direction: column;
    align-items: center;
    float: left;
    width: 25%;
    position: absolute;
    top: 0px;
    bottom: 50px;
    .r_l_item {
      width: 100%;
      position: relative;
      span {
        padding: 10px;
        font-size: 12px;
        text-align: center;
        display: block;
        top: 50%;
      }
    }
    .em_l_selected {
      background-color: white;
    }
  }
  .r_l_item_divider:before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    bottom: auto;
    right: auto;
    height: 0.5px;
    width: 100%;
    background-color: #ebeced;
  }
  .r_right {
    overflow-y: auto;
    overflow-x: hidden;
    -webkit-overflow-scrolling: touch;
    display: flex;
    flex-direction: column;
    align-items: center;
    float: right;
    width: 75%;
    position: absolute;
    top: 0px;
    right: 0px;
    bottom: 50px;
    .r_r_item {
      position: relative;
      width: 100%;
      .r_r_i_image {
        float: left;
        width: 80px;
        height: 100%;
        img {
          margin: 10px 0 0 10px;
          width: 70px;
          height: 70px;
        }
      }
      .r_r_i_detail {
        padding: 10px;
        height: 100%;
        width: auto;
        overflow: hidden;
        .r_r_i_d_title {
          font-size: 15px;
        }
        .r_r_i_d_info {
          font-size: 8px;
          color: #666566;
        }
        .r_r_i_d_buy {
          display: flex;
          justify-content: space-between;
          align-items: center;
          .r_r_i_d_b_price {
            color: #fb6c6c;
          }
          .r_r_i_d_b_spec {
            position: relative;
            .r_r_i_d_b_s_choose {
              padding: 2px 7px;
              border: 1px solid #adb6bd;
              border-radius: 13px;
              font-size: 11px;
            }
            .r_r_i_d_b_s_count {
              position: absolute;
              top: -5px;
              right: -5px;
              font-size: 8px;
              border-radius: 8px;
              background-color: #fb6c6c;
              color: white;
              padding: 3px;
              height: 10px;
              min-width: 10px;
              line-height: 10px;
              text-align: center;
            }
          }
          .r_r_i_d_b_operate {
            display: flex;
            align-items: center;
            span {
              margin: 0 5px;
            }
            img {
              width: 23px;
              height: 23px;
            }
          }
        }
      }
    }
    .r_r_item_divider:before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      bottom: auto;
      right: auto;
      height: 1px;
      width: 100%;
      background-color: #ebeced;
    }
  }
  .r_background_cover {
    position: fixed;
    background-color: #666566;
    bottom: 0;
    opacity: 0.3;
    height: 100%;
    width: 100%;
    z-index: 100;
  }
  .r_shoping_cart {
    position: fixed;
    bottom: 0;
    width: 100%;
    z-index: 100;
    .r_sc_detail {
      background-color: white;
      .r_sc_d_title {
        position: relative;
        height: 50px;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        img {
          width: 22px;
          height: 22px;
          margin-right: 3px;
        }
        span {
          font-size: 15px;
          color: #fca511;
          margin-right: 10px;
        }
      }
      .r_sc_d_title:before {
        content: "";
        position: absolute;
        left: 0;
        top: auto;
        bottom: 0;
        right: auto;
        height: 1px;
        width: 100%;
        background-color: #ebeced;
      }
      ul {
        margin: 0;
        padding: 0;
        list-style: none;
        max-height: 250px;
        overflow-y: auto;
        overflow-x: hidden;
        .r_sc_d_item {
          position: relative;
          border:1px solid transparent;
          display: flex;
          align-items: center;
          padding: 8px 0;
          .r_sc_d_i_goods_name {
            width: 55%;
            display: flex;
            flex-direction: column;
            span {
              margin-left: 10px;
              white-space: nowrap;
              text-overflow: ellipsis;
              overflow: hidden;
            }
            .r_sc_d_i_gn_main {
              font-size: 14px;
            }
            .r_sc_d_i_gn_sub {
              color: #666566;
              font-size: 10px;
            }
          }
          .r_sc_d_i_goods_detail {
            width: 45%;
            display: flex;
            justify-content: space-between;
            .r_sc_d_i_gd_price {
              color: #fb6c6c;
            }
            .r_sc_d_i_gd_operate {
              display: flex;
              align-items: center;
              padding-right: 10px;
              span {
                margin: 0 5px;
              }
              img {
                width: 23px;
                height: 23px;
              }
            }
          }
        }
        .r_sc_d_item:before {
          content: "";
          position: absolute;
          left: 0;
          top: auto;
          bottom: 0px;
          right: auto;
          height: 1px;
          width: 100%;
          background-color: #ebeced;
        }
      }
    }
    .r_sc_icon {
      position: absolute;
      background-color: #fca511;
      width: 50px;
      height: 50px;
      left: 10px;
      bottom: 12px;
      border-radius: 25px;
      img {
        position: relative;
        top: 12.5px;
        left: 12.5px;
        width: 25px;
        height: 25px;
      }
      span {
        position: absolute;
        text-align: center;
        top: 0;
        right: -10px;
        padding-left: 5.5px;
        padding-right: 5.5px;
        height: 20px;
        border-radius: 10px;
        background-color: #fb6c6c;
        border: 1px solid #f2f3f4;
        box-sizing: border-box;
        font-size: 12px;
        color: #f2f3f4;
        line-height: px;
      }
    }
    .r_sc_recommend {
      background-color: #fde3b9;
      height: 28px;
      text-align: center;
      span {
        height: 28px;
        line-height: 28px;
        font-size: 13px;
      }
    }
    .r_sc_bottom {
      height: 50px;
      width: 100%;
      display: flex;
      align-items: center;
      text-align: center;
      .r_sc_b_total {
        width: 70%;
        justify-content: left;
        span {
          padding-left: 90px;
        }
      }
      .r_sc_b_submit {
        width: 30%;
        background-color: chocolate;
        justify-content: center;
      }
      div {
        height: 100%;
        display: flex;
        align-items: center;
      }
    }
  }
  .r_choose {
    position: fixed;
    background-color: rgba(102, 101, 102, 0.5);
    bottom: 0;
    height: 100%;
    width: 100%;
    z-index: 105;
    display: flex;
    justify-content: center;
    align-items: center;
    .r_c_detail {
      position: absolute;
      background-color: white;
      border-radius: 5px;
      left: 50px;
      right: 50px;
      padding: 15px;
      max-width: 85%;
      .r_c_d_title {
        height: 20px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        span {
          font-size: 15px;
        }
        img {
          width: 17px;
          height: 17px;
        }
      }
      .r_c_d_content {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: 10px 0;
        .r_c_d_c_left {
          width: 85px;
          align-self: flex-start;
          font-size: 14px;
          color: #666566;
        }
        .r_c_d_c_right {
          display: flex;
          flex-wrap: wrap;
          .r_c_d_c_r_item {
            border: 0.5px solid #b1b2b3;
            color: #b1b2b3;
          }
          .r_c_d_c_r_item_selected {
            border: 0.5px solid #fca511;
            color: #fca511;
          }
          span {
            font-size: 10px;
            margin: 0 5px 3px 0;
            border-radius: 13px;
            padding: 2px 5px;
          }
        }
      }
      .r_c_d_bottom {
        height: 34px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .r_c_d_b_price {
          color: #fb6c6c;
        }
        .r_c_d_b_submit {
          height: 34px;
          line-height: 36px;
          background-color: #fca511;
          color: white;
          border-radius: 8px;
          font-size: 15px;
          padding: 0 10px;
        }
        .r_c_d_b_operate {
          display: flex;
          align-items: center;
          padding-right: 10px;
          span {
            margin: 0 5px;
          }
          img {
            width: 23px;
            height: 23px;
          }
        }
      }
    }
  }
}
