

















































.store_root {
  background-color: white;
  text-align: center;
  .s_logo {
    width: 375px;
    height: 265px;
  }
  .s_info {
    .s_i_item {
      position: relative;
      margin: 20px;
      text-align: left;
      display: flex;
      img {
        width: 18px;
        height: 18px;
        margin-top: 2px;
        margin-right: 10px;
      }
      span {
        font-size: 15px;
        a {
          text-decoration: none;
          color: black;
        }
      }
    }
    .s_i_i_title {
      img {
        margin-top: 4px;
      }
      span {
        color: #fca511;
        font-size: 18px;
      }
    }
    .s_i_item:before {
      content: '';
      position: absolute;
      left: 0;
      top: auto;
      bottom: -10px;
      right: 0;
      height: 1px;
      width: 100%;
      background-color: #EBECED;
    }
  }
  .s_other {
    text-align: left;
    span {
      margin: 20px;
    }
  }
}
