.is_root {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  text-align: center;
  height: 100%;
}
.is_root .is_window {
  position: relative;
  overflow: hidden;
  margin: 0 auto;
  height: 100%;
}
.is_root .is_window .is_container {
  position: absolute;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.is_root .is_window .is_container li {
  list-style: none;
}
.is_root .is_window .is_container li img {
  width: 375px;
  height: 180px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.is_root .is_window .is_direction li {
  list-style: none;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 30px;
