.store_root {
  background-color: white;
  text-align: center;
}
.store_root .s_logo {
  width: 375px;
  height: 265px;
}
.store_root .s_info .s_i_item {
  position: relative;
  margin: 20px;
  text-align: left;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.store_root .s_info .s_i_item img {
  width: 18px;
  height: 18px;
  margin-top: 2px;
  margin-right: 10px;
}
.store_root .s_info .s_i_item span {
  font-size: 15px;
}
.store_root .s_info .s_i_item span a {
  text-decoration: none;
  color: black;
}
.store_root .s_info .s_i_i_title img {
  margin-top: 4px;
}
.store_root .s_info .s_i_i_title span {
  color: #fca511;
  font-size: 18px;
}
.store_root .s_info .s_i_item:before {
  content: '';
  position: absolute;
  left: 0;
  top: auto;
  bottom: -10px;
  right: 0;
  height: 1px;
  width: 100%;
  background-color: #EBECED;
}
.store_root .s_other {
  text-align: left;
}
.store_root .s_other span {
  margin: 20px;
}
