.tab_slider_root {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 45px;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  background-color: white;
}
.tab_slider_root div {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.tab_slider_root div span {
  padding: 0 10px;
  color: #323334;
  font-size: 15px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  vertical-align: middle;
}
.tab_slider_root .ts_selected span {
  color: #fca511;
}
.tab_slider_root .ts_selected:before {
  content: "";
  position: absolute;
  top: auto;
  bottom: 0;
  left: auto;
  right: auto;
  height: 1px;
  width: 100%;
  background-color: #fca511;
}
.tab_slider_root:before {
  content: "";
  position: absolute;
  left: 0;
  top: auto;
  bottom: 0;
  right: auto;
  height: 1px;
  width: 100%;
  background-color: #EBECED;
}
