



















































































































































.is_root {
  box-sizing: border-box;
  text-align: center;
  height: 100%;
  .is_window {
    position: relative;
    overflow: hidden;
    margin: 0 auto;
    height: 100%;
    .is_container {
      position: absolute;
      display: flex;
      li {
        list-style: none;
        img {
          width: 375px;
          height: 180px;
          user-select: none;
        }
      }
    }
    .is_direction {
      li {
        list-style: none;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        width: 30px;
        height: 30px;
        background-color: rgba(255, 255, 255, 0.3);
        border-radius: 50%;
        img {
            width: 100%;
            height: 100%;
        }
      }
      .is_direction_left {
        left: 3%;
      }
      .is_direction_right {
        right: 3%;
      }
    }
    .is_segment_control {
      position: absolute;
      bottom: 10px;
      left: 50%;
      transform: translateX(-50%);
      .is_segment_selected {
        background-color: red;
      }
      li {
        display: inline-block;
        background-color: #333;
        margin: 0 3px;
        width: 8px;
        height: 8px;
        border-radius: 50%;
        cursor: pointer;
      }
    }
  }
}
