






























































































.order_root {
  .o_header {
    position: fixed;
    top: 0;
    height: 45px;
    width: 100%;
    z-index: 100;
  }
  .o_contanier {
    position: absolute;
    width: 100%;
    top: 45px;
    bottom: 0px;
  }
  .o_footer {
    position: fixed;
    bottom: 0;
    height: 50px;
    width: 100%;
    z-index: 100;
    background-color: red;
  }
}
