          box-shadow: -5px 0 4px rgba(0, 0, 0, 0.1), 0 -5px 4px rgba(0, 0, 0, 0.1), 0 0 0 rgba(0, 0, 0, 0), 5px 0 4px rgba(0, 0, 0, 0.1);
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.p_root .p_user .p_u_vip .p_uv_left {
  margin-left: 15px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.p_root .p_user .p_u_vip .p_uv_left div {
