

























































































.p_root {
  .p_user {
    background-color: #FEFEFF;
    height: 250px;
    .p_u_info {
      height: 70%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      img {
        background-color: aqua;
        width: 70px;
        height: 70px;
        border-radius: 35px;
      }
      span {
        margin-top: 10px;
        font-size: 14px;
      }
    }
    .p_u_vip {
      height: 30%;
      margin: 0 20px;
      border-radius: 8px 8px 0 0;
      box-shadow: -5px 0 4px rgba(0, 0, 0, .1), //左边阴影
                  0 -5px 4px rgba(0, 0, 0, .1), //顶部阴影
                  0 0 0 rgba(0, 0, 0, 0), //底部阴影
                  5px 0 4px rgba(0, 0, 0, .1); //右边阴影
      align-items: center;
      display: flex;
      flex-wrap: nowrap;
      justify-content: space-between;
      .p_uv_left {
        margin-left: 15px;
        display: flex;
        flex-direction: column;
        div {
          display: flex;
          align-items: center;
          img {
            width: 17px;
            height: 17px;
            margin-right: 4px;
          }
        }
        .p_uv_main_title {
          font-weight: 800;
          font-size: 17px;
        }
        .p_uv_sub_title {
          margin-top: 3px;
          font-weight: 400;
          font-size: 14px;
        }
      }
      .p_uv_right {
        height: 60%;
        margin-right: 15px;
        span {
          border: 1px solid;
          font-size: 14px;
          padding: 4px 8px;
          border-radius: 5px;
        }
      }
    }
    .p_u_menu {
      width: 100%;
    }
  }
}
