



































































.evaluation_root {
  .e_header {
    padding-top: 20px;
    margin-bottom: 20px;
    text-align: center;
    .e_h_score {
      .e_h_s_value {
        font-size: 29px;
      }
      .e_h_s_unit {
        font-size: 9px;
      }
      span {
        color: #FB6C6C;
      }
    }
    .e_h_comment_count {
      font-size: 17px;
      color: #878889;
    }
  }
  .e_container {
    .e_c_item {
      position: relative;
      padding: 10px;
      .e_c_i_title {
        display: flex;
        justify-content: space-between;
        .e_c_i_t_left {
          display: flex;
          flex-wrap: wrap;
          .e_c_i_t_name {
            font-size: 15px;
            color: #878889;
          }
          .e_c_i_t_star {
            list-style: none;
            padding: 0 0 0 5px;
            margin: 0;
            .e_c_i_t_s_item {
              padding: 0;
              float: left;
              img {
                padding: 0;
                margin: 0;
                width: 18px;
                height: 18px;
              }
            }
          }
        }
        .e_c_i_t_date {
          font-size: 15px;
          color: #878889;
          white-space: nowrap;
        }
      }
      .e_c_i_detail {
        margin-top: 10px;
        .e_c_i_d_content {
          font-size: 15px;
        }
        .e_c_i_d_images {
          display: flex;
          flex-wrap: wrap;
          img {
            width: 50px;
            height: 50px;
            margin-top: 10px;
            margin-right: 10px;
          }
        }
      }
    }
    .e_c_item:before {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      bottom: auto;
      right: auto;
      height: 0.5px;
      width: 100%;
      background-color: #EBECED;
    }
  }
}
